const axios = require("axios");
//toast_time 
const toast_time = 1000;

export default {
  data: function() {
    return {
      account: "",
      password: "",
      name: ""
    };
  },
  created: async function() {
    //
    /**@augments
     * ????? dnxntoken
     */
    this.$vux.loading.show({
      text: "..."
    });
    let login_res = await axios({
      url: this.$common.checkUserUrl,
      method: "get"
    });
    this.$vux.loading.hide();
    console.log("checkUser_result:" + login_res.data.userLogin);
    if (login_res.data.userLogin) {
      this.$vux.toast.show({
        text: "",
        time: toast_time
      });
      setTimeout(() => {
        this.$router.push({ name: "searchPage" });
      }, toast_time);
    } else {
      this.$vux.toast.show({
        text: ",",
        time: 2000,
        type: "warn"
      });
    }
  },
  methods: {
    confirm: async function() {
      console.log(this.account, this.password, this.name);
      //loading 
      this.$vux.loading.show({
        text: "",
        isShowMask: true
      });
      let result = await axios({
        method: "post",
        data: {
          stuId: this.account,
          pswd: this.password,
          name: this.name
        },
        url: this.$common.rebindUrl
      });
      if (result.data.success) {
        //
        this.$vux.loading.hide();
        this.$vux.toast.show({
          text: "",
          type: "success",
          isShowMask: true,
          time: toast_time
        });
        //searchPage
        setTimeout(() => {
          this.$router.push({ name: "searchPage" });
        }, toast_time);
      } else {
        //
        this.$vux.loading.hide();
        this.$vux.toast.show({
          text: result.data.err.msg,
          type: "cancel",
          isShowMask: true,
          time: toast_time
        });
      }
      console.log(result.data);
    }
  },
  components: {}
};



