export default {
  props: ["searchStrFromFather"],
  data: function() {
    return {
      searchStr: ""
    };
  },
  methods: {
    doSearch: function() {
      //console.log("");
      this.$emit("search", this.searchStr);
    },
    //
    toSearchInfoPage: function() {
      //console.log("");
      this.$emit("gotoinfo");
    }
  },
  watch: {
    searchStr: function() {
      this.doSearch();
    }
  },
  created: function() {
    this.searchStr = this.searchStrFromFather;
  }
};



