const parseStr = function(str) {
  let patt1 = //g;
  let patt2 = //g;
  let patt3 = //g;
  let patt4 = /\d\d\d\d-\d\d-\d\d/g;
  let strPrefix = str.match(patt1);
  if (strPrefix == null) {
    // 
    strPrefix = str.match(patt2);
  }
  if (strPrefix == null) {
    // 
    strPrefix = str.match(patt3);
  }
  strPrefix = strPrefix[0];
  let strSuffix = str.match(patt4)[0];
  return strPrefix + ": " + strSuffix;
};
export default {
  props: ["books"],
  data: function() {
    return {
      myBooks: [],
      isNowBorrow: false,
      isHisBorrow: false
    };
  },
  methods: {
    gotoBookDetail: function(book) {
      const _this = this;
      if (book.name === "") {
        this.$router.push({
          name: "bookMorePage",
          params: {
            books: _this.books
          }
        });
        return;
      }
      this.$router.push({
        name: "bookDetail",
        params: {
          book: book
        }
      });
    }
  },
  watch: {
    books: async function() {
      //nowborrow
      if (this.books[0].lawBackDate != undefined) {
        this.isNowBorrow = true;
        for (let i = 0; i < this.books.length; i++) {
          this.books[i].lawBackDate = parseStr(this.books[i].lawBackDate);
          this.books[i].borrowDate = parseStr(this.books[i].borrowDate);
        }
      }
      //hisBorrow
      if (this.books[0].returnDate != undefined) {
        this.isHisBorrow = true;
        for (let i = 0; i < this.books.length; i++) {
          this.books[i].returnDate = parseStr(this.books[i].returnDate);
          this.books[i].borrowDate = parseStr(this.books[i].borrowDate);
        }
      }
      //
      let cover_books = [];
      if (this.books.length >= 5) {
        cover_books = this.books.slice(0, 5);
      } else {
        cover_books = this.books.slice(0, this.books.length);
      }
      //cover_books
      if (this.books.length > 5) {
        cover_books[cover_books.length] = {
          name: "",
          bookId: "",
          isMoreCover: true
        };
      }
      /**@augments
       * cover_booksdetail
       * 1.booksId 
       * 2.commongetManyImgUrlimgurl
       * 3.cover_booksimgs
       */
      let bookIds = [];
      for (let i = 0; i < cover_books.length; i++) {
        let patt = /\d+/g;
        //
        if (cover_books[i].name !== "") {
          bookIds[i] = cover_books[i].bookId.match(patt)[0];
        }
      }

      let manyBooks = [];
      manyBooks = await this.$common.getmanyImgUrl(bookIds);

      for (let i = 0; i < cover_books.length; i++) {
        //
        if (cover_books[i].name !== "") {
          cover_books[i].imgUrl = manyBooks[i].imgurl;
        }
        this.myBooks = [];
        this.myBooks = cover_books;
      }
      this.myBooks = cover_books;
    }
  },
  created: async function() {}
};



